<script>
import Delete from "./Delete.vue";

export default {
  name: "List",
  components: { Delete },
  props : {
    ListSchools: {
      type: Array,
      default: () => []
    }
 },

  data() {
    return {
      textSearch: '',
    };
  },

  computed: {
    get_openDialogDelete() {
      return this.$store.getters["uploadSchools/get_openDialogDelete"];
    },
  },

  methods: {
    async getSchools() {
      return await this.$store.dispatch("uploadSchools/getListSchools");
    },

    async searchSchools() {
      await this.$store.commit("uploadSchools/set_textSearch", this.textSearch);
      clearTimeout(this.debounceTimeout);
      this.debounceTimeout = setTimeout(() => {
        this.getSchools();
      }, 300);
    },

    editSchool(school) {
      this.$store.commit("uploadSchools/set_selectSchool", school);
      this.$store.commit("uploadSchools/set_openDialogForm", true);
    },

    async deleteSchool(school) {
      this.$store.commit("uploadSchools/set_selectSchool", school);

      this.$store.commit("uploadSchools/set_openDialogDelete", true);
    },
  }
}
</script>

<template>
  <div class="mt-5" >
    <vs-table striped class="cursor-pointer" >
      <template #header>
        <div  class="text-msj">(Ejemplo de b√∫squeda: NUESTRA SE√ëORA DE FATIMA-<b>IQUITOS</b>). Usa solo un guion para separar el nombre y el distrito, para mejorar la b√∫squeda.</div>
        <vs-input block class="" primary state="primary"  type="search"    placeholder="Buscar colegio"  v-model="textSearch"
                  @input="searchSchools" />
      </template>

      <template #thead>
        <vs-tr>
          <vs-th>
            Colegio
          </vs-th>
          <vs-th>
            Distrito
          </vs-th>
          <vs-th></vs-th>
        </vs-tr>
      </template>
      <template #tbody>
        <vs-tr
            :key="i"
            v-for="(tr, i) in ListSchools"
            :data="tr"
        >
          <vs-td>
            {{ tr.name }}
          </vs-td>
          <vs-td>
            {{ tr.district }}
          </vs-td>
          <vs-td class="flex items-center justify-center">
            <vs-button icon color="primary" @click="editSchool(tr)" ><i class="fas fa-pen"/></vs-button>
            <vs-button icon  color="danger" @click="deleteSchool(tr)" ><i class="fas fa-trash"/></vs-button>
          </vs-td>
        </vs-tr>
      </template>

      <template #notFound>
        No se encontraron datos. üò¢
      </template>
    </vs-table>
    <Delete v-if="get_openDialogDelete" />
  </div>
</template>

<style scoped lang="scss">
.text-msj {
  font-size: 0.6rem;
  color: #950101;
  margin-top: 5px;
  transition: all 0.3s;
}
