<script>
export default {
  name: "Form",

  data() {
    return {
      nameSchool: '',
      district: '',
      activeForm:true,
      isLoading: false
    };
  },

  computed: {
    get_selectSchool() {
      return this.$store.getters["uploadSchools/get_selectSchool"];
    },
  },

  methods: {

    checksSpecialCharacters(){
      let specialCharacters = ['.',',','+','-','*','/','@','#','$','%','&','(',')','[',']','{','}','?','¬ø','!','¬°','"','\'','¬¥','`','¬®','^','~',';',':','<','>','|','¬∞','¬¨','\\','/','¬∫','¬™','‚Ç¨','¬•','¬¢','¬£','¬ß','¬©','¬Æ','‚Ñ¢','‚ÑÖ','‚Ñì','‚Ñ¶','‚ÑÆ','‚Ñó','‚Ññ','‚Ñû','‚Ñü']

      return this.district.split('').some( item => specialCharacters.includes(item) )
    },

    verifyForm() {
      return this.nameSchool.trim() !== '' && this.district.trim() !== '';
    },

    closeForm() {
      if(this.get_selectSchool) this.$store.commit("uploadSchools/set_selectSchool", null);

      this.$store.commit("uploadSchools/set_openDialogForm", false);

    },

    initialData() {
      if(this.get_selectSchool){
        this.nameSchool = this.get_selectSchool.name;
        this.district = this.get_selectSchool.district;
      }
    },

    async saveData(){

      if (!this.verifyForm()) {
        this.$vs.notification({
          title: "Error üòì",
          text: "Por favor, complete los campos. üëç",
          color: "danger",
        });

        return;
      }

      if(this.checksSpecialCharacters()){
        this.$vs.notification({
          title: "Error üòì",
          text: "No se permiten caracteres especiales en el campo distrito.",
          color: "danger",
        });

        return;
      }

      this.isLoading = true;

      let data = {
        name: this.nameSchool.trim().toUpperCase(),
        district: this.district.trim().toUpperCase()
      };

      if (this.get_selectSchool) data = {...data, id: this.get_selectSchool.id};

      const status = this.get_selectSchool ? await this.$store.dispatch("uploadSchools/updateSchool", data) : await this.$store.dispatch("uploadSchools/createSchool", data);

      if (status.message) await this.$store.dispatch("uploadSchools/getListSchools");

      this.$vs.notification({
        title: status.message ? "√âxito üòÉ" : "Alerta ‚ö†Ô∏è",
        text: status.message ? `${ status.message} üëç` : `${status.error}`,
        color: status.message ? "success" : "danger",
      });

      this.isLoading = false;

      if(status.message) this.closeForm();

    }

  },

  mounted() {
    this.initialData();
  },

}
</script>

<template>
  <div>
    <vs-dialog prevent-close blur v-model="activeForm"  v-on:close="closeForm" >
      <template #header>
        <h4 class="not-margin">
          {{get_selectSchool ? 'Actualizar' : 'Registrar'}} <b>Colegio</b>
        </h4>
      </template>

      <div>
        <form class="space-y-7" >
          <vs-input block primary type="text" label="Nombre del colegio" v-model="nameSchool" />

          <vs-input block primary type="text" label="Distrito" v-model="district" @keyup.enter="saveData" />
        </form>
      </div>

      <template #footer>
        <vs-button block :loading="isLoading" primary @click="saveData" >Guardar</vs-button>
      </template>
    </vs-dialog>
  </div>
</template>

