<script>
import ImportExcel from "@/views/upload_schools/components/ImportExcel.vue";
import List from "@/views/upload_schools/components/List";
import Form from "./components/Form.vue"

export default {
  components: { List, ImportExcel, Form },

  data() {
    return {
      isForm : false
    };
  },

  computed: {
    schools() {
      return this.$store.getters["uploadSchools/get_school"];
    },
    get_openDialogForm() {
      return this.$store.getters["uploadSchools/get_openDialogForm"];
    },
  },

  methods: {
    async getListSchools() {
      await this.$store.dispatch("uploadSchools/getListSchools");
    },

    openDialog() {
      this.$store.commit("uploadSchools/set_openDialogForm", true);
    },

  },

  mounted() {
    this.getListSchools();
  },
};
</script>

<template>
  <div>
    <Form v-if="get_openDialogForm"/>
    <import-excel />
    <div class="mt-4">
      <vs-button primary @click="openDialog">Registrar Colegio</vs-button>
    </div>
    <list :list-schools="schools" />
  </div>
</template>
