<script>
export default {
  name: "delete",

  data() {
    return {
      isActive: true,
      isLoading: false
    };
  },

  computed: {
    get_selectSchool() {
      return this.$store.getters["uploadSchools/get_selectSchool"];
    },
  },

  methods: {
    closeDelete() {
      this.$store.commit("uploadSchools/set_selectSchool", null);

      this.$store.commit("uploadSchools/set_openDialogDelete", false);
    },

    async deleteData(){

      if (!this.get_selectSchool) return

      this.isLoading = true;

      const status = await this.$store.dispatch("uploadSchools/deleteSchool", this.get_selectSchool.id);

      if(status) await this.$store.dispatch("uploadSchools/getListSchools");

        this.$vs.notification({
          title: status ? "√âxito üòÉ" : "Error üòì",
          text:status ? "El colegio ha sido eliminado correctamente. üëç": "Ocurri√≥ un error al eliminar el colegio. üòì",
          color: status ? "success" : "danger",
        });

        this.isLoading = false;

        if (status) this.closeDelete();
    }

  }

}
</script>

<template>
  <div>
    <vs-dialog blur prevent-close v-model="isActive" v-on:close="closeDelete" width="400px" >
      <template #header>
        <h4 class="vs-dialog__header__title">Eliminar <b>Colegio</b></h4>
      </template>

      <main>
        <div class="flex flex-col justify-center space-y-2">
          <p class="text-red-500" >¬øEst√°s seguro que deseas eliminar este colegio?</p>
          <b>{{get_selectSchool.name}}</b>
          <p>Esta acci√≥n no se puede deshacer una vez confirmada.</p>
        </div>
      </main>

      <template #footer>
        <div class="flex justify-end">
          <vs-button :disabled="isLoading" shadow @click="closeDelete">Cancelar</vs-button>
          <vs-button :loading="isLoading"  @click="deleteData">Confirmar</vs-button>
        </div>
      </template>

    </vs-dialog>
  </div>
</template>

